@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700&display=swap);

.utility-bar {
    height: 30px;
    background: linear-gradient(90deg, rgba(33,62,124,1) 0%, rgba(54,175,144,1) 100%);
    padding-top: 5px;
    font-size: 13px;
}
.utility-bar-buttons-container {
    width: 100%;
    grid-gap: 2em;
    gap: 2em;
}

@media (min-width: 1200px) { 
    .utility-bar-buttons-container {
        width: 90%;
        grid-gap: 1em;
        gap: 1em;
    }
}
.utility-button-container {
    grid-gap: 1em;
    gap: 1em;
}
a.utility-button {
    background-color: transparent;
    font-weight: bold;
    color: #fff;
    border: none;
    text-decoration: none;
}
.header-container {
    width: 100%;
    padding-left: 8px;
    padding-right: 8px;
    z-index: 1000 !important
}

@media (min-width: 992px) {
    .header-container {
        width: 100%;
        padding-left: 150px;
        padding-right: 150px;
    }
    .navbar .has-megamenu {
        position: static !important;
    }
    .dropdown-menu {
        display: none;
        position: absolute;
        border: none;
        width: 100%;
        left: 0;
    }
    .dropdown-menu.show {
        padding: 0px;
    }
    .dropdown-item-text {
        padding: 0px;
    }
}

.header {
    height: 66px;
}

.header-link {
    color: #2e4b8a;
}

.dropdown-toggle:after {
    content: none !important
}
.megamenu-container {
    width: 100%;
    color: rgb(34, 34, 34);
}

@media (min-width: 992px) {
    .megamenu-container {
        width: 100%;
        padding-left: 120px;
        padding-right: 0px;
    }
}

.title {
    height: 80px;
    font-size: 40px;
    font-weight: bold;
    font-family: "Nunito Sans";
}

.product {
    height: 52px;
}

.product-name {
    color: rgb(33, 82, 153);
}

.product-name:hover, .product-name:focus {
    font-weight: bolder;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    border-bottom-width: 3px;
    border-bottom-style: solid;
    border-bottom-color: #40c8f4;
}

.product-content {
    background-color: #f6f6f6;
    grid-gap: 3em;
    gap: 3em;
}

.product-snippet-title {
    font: "Nunito Sans";
    font-weight: bold;
    color: rgb(71, 71, 71);
}

.card-title {
    font: "Nunito Sans";
    font-weight: bold;
    color: rgb(41, 41, 41);
}

.white-paper {
    color: purple;
    font-size: 15px;
}

.white-paper-link {
    color: rgb(71, 71, 71);
    font-size: 20px;
}

.white-paper-link:hover, .white-paper-link:focus {
    color: rgb(71, 71, 71);
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    border-bottom-width: 3px;
    border-bottom-style: solid;
    border-bottom-color: #81bc00;
}
.hero {
    height: 100vh;
    background-image: url(/teradyne/static/media/background.cc239c21.png);
}

@media (min-width: 1200px) {
    .hero {
        height: 836px;
        padding-left: 100px;
    }
}


.hero-text {
    color: #fff;
    font-family: "Nunito Sans";
    font-weight: bold;
    text-shadow: 2px 3px 5px rgba(0,0,0,0.5);
}

.learn-more-button {
    background-color: #81bc00;
    font-weight: bold;
    font-size: smaller;
    padding: 20px;
    padding-left: 30px;
    padding-right: 30px;
    border: none;
}
.industries-container {
    margin-top: 5em;
    min-height: 100vh;
    padding-bottom: 2em;
    color: rgb(84, 88, 90);
    font-family: "Nunito Sans";
    grid-gap: 5em;
    gap: 5em;
}

.expertise {
    font-weight: 700;
}
.carousel-container {
    grid-gap: 2em;
    gap: 2em;
}
.industry-image {
    border: 1px;
    border-radius: 2%;
    box-shadow: 10px 11px 40px -3px rgba(0, 0, 0, 0.27);
    -webkit-box-shadow: 10px 11px 40px -3px rgba(0, 0, 0, 0.27);
    -moz-box-shadow: 10px 11px 40px -3px rgba(0, 0, 0, 0.27);
}

.industrial-automation-content {
    grid-gap: 1rem;
    gap: 1rem;
}
.industrial-automation-text {
    font-size: 1.3em;
    line-height: 2em;
}

.explore-button {
    background-color: #81bc00;
    width: 85%;
    font-family: "Nunito Sans";
    font-weight: bold;
    font-size: small;
    padding: 20px;
    border: none;
}
.carousel-dots {
    grid-gap: calc(5px + 0.5em);
    gap: calc(5px + 0.5em);
}
.dot {
    height: 10px;
    width: 10px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
  }

.dot.green {
    background-color: #7eb800;
}
.applications-section-container {
    padding-top: 5em;
    background-color: #333333;
    min-height: 70vh;
    padding-bottom: 2em;
    color: rgb(253, 253, 253);
    font-family: "Nunito Sans";
}

.applications-title {
    font-weight: bold;
}

@media (min-width: 992px) {
    .applications-subtitle {
        width: 50%;
    }
}
.application-logos {
    margin-top: 8em;
    grid-gap: 4em;
    gap: 4em;
    font-weight: bold;
}
.application-logo-name-container {
    grid-gap: 2em;
    gap: 2em;
    font-size: larger;
}
.news-and-events-container {
    margin-top: 5em;
    min-height: 100vh;
    padding-bottom: 2em;
    color: rgb(84, 88, 90);
    font-family: "Nunito Sans";
    grid-gap: 5em;
    gap: 5em;
}

.newsAndEventsTitle {
    font-weight: 700;
}

.news-column {
    grid-gap: 3em;
    gap: 3em;
}

.read-more-link {
    color: rgb(71, 71, 71);
}

.read-more-link:hover, .read-more-link:focus {
    color: rgb(71, 71, 71);
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    border-bottom-width: 3px;
    border-bottom-style: solid;
    border-bottom-color: #81bc00;
}

.view-all-button {
    background-color: #81bc00;
    font-family: "Nunito Sans";
    font-weight: bold;
    font-size: small;
    padding: 20px;
    border: none;
}

.tugx-container {
    background: linear-gradient(172deg, rgba(10,175,225,1) 0%, rgba(82,180,128,1) 100%);
    grid-gap: 2em;
    gap: 2em;
    color: white;
}
.invest-container {
    min-height: 90vh;
    background: url(/teradyne/static/media/background.0b706041.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    color: rgb(48, 48, 48);
    grid-gap: 6rem;
    gap: 6rem;
}

.invest-title {
    font-weight: 500;
    font-family: "Nunito Sans";
}

.contact-us-button {
    background-color: white;
    color: rgb(84, 88, 90);
    font-family: "Nunito Sans";
    font-size: small;
    font-weight: bold;
    border: none;
    padding-top: 20px;
    padding-bottom: 15px;
    padding-left: 40px;
    padding-right: 40px;
}

.invest-number {
    color: white;
    font-size: 6em;
    font-weight: bold;
    font-family: "Nunito Sans";
}

.invest-text {
    font-family: "Nunito Sans";
    font-weight: bold;
    color: white;
}
.teradyne-footer {
    min-height: 50vh;
    color: #2e4b8a;
}

.links-columns-container {
    grid-gap:1em;
    gap:1em;
}
.links-column {
    grid-gap:1em;
    gap:1em;
}

.subscribe-text {
    font-size: 0.9rem;
}

a {
    text-decoration: none;
    color: #2f5d9f;

}
.subscribe-input:focus {
    outline: none;
}

.subscribe-input {
    border-color: gray;
    border-width: 1px;
}
@font-face {
    font-family: "Gotham Narrow";
    font-weight: 400;
    src: url(/teradyne/static/media/GothamNarrow-Light.94b2d91c.otf) format("opentype");
}

@font-face {
    font-family: "Gotham Narrow";
    font-weight: 700;
    src: url(/teradyne/static/media/GothamNarrow-Bold.3f7d2101.otf) format("opentype");
}

@font-face {
    font-family: "Gotham Narrow";
    font-weight: 900;
    src: url(/teradyne/static/media/GothamNarrow-Black.95941c8c.otf) format("opentype");
}

@font-face {
    font-family: "Gotham";
    src: url(/teradyne/static/media/Gotham-Medium.b23bb62d.ttf) format("truetype");
}

@font-face {
    font-family: "Gotham";
    font-weight: 900;
    src: url(/teradyne/static/media/Gotham-Ultra.e9e7ccff.ttf) format("truetype");
}

body {
    font-family: "Gotham Narrow";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
